import React, { useState } from 'react';
import "./TweetBox.css";
import { Avatar, Button } from "@material-ui/core";
import db from './firebase';

function TweetBox() {

    const [tweetMessage, setTweetMessage] = useState("");
    const [tweetImage, setTweetImage] = useState("");

    const sendTweet = (e) => {
        e.preventDefault();

        db.collection("posts").add({
            displayName: "Akash Chopra",
            username: "akashchopra",
            verified: true,
            text: tweetMessage,
            image: tweetImage,
            avatar: "data:image/png;base64,"
        });

        setTweetMessage("");
        setTweetImage("");
    };

    return (
        <div className="tweetBox">
            <form>
                <div className="tweetBox__input">
                    <Avatar src="https://pbs.twimg.com/profile_images/1385269922553470976/D4ZnmXjy_400x400.jpg" />
                    <input 
                        onChange={(e) => setTweetMessage(e.target.value)}
                        value={tweetMessage}    
                        placeholder="What's happening?" 
                        type="text"
                    />
                </div>
                <input 
                    onChange={(e) => setTweetImage(e.target.value)}
                    value={tweetImage}
                    className="tweetBox__inputImage"
                    placeholder="Optional: Enter image URL" 
                    type="text" 
                />
                <Button type="submit" className="tweetBox__tweetButton" onClick={sendTweet}>Tweet</Button>
            </form>
        </div>
    )
}

export default TweetBox;
